* {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

#wrapper {
  box-sizing: border-box;
  background-image: radial-gradient(circle at top, #746f64 0%, #2f2d29 100%);
  width: 100%;
  height: 100vh;
  padding: 40px;
}

h1 {
  text-align: center;
  color: #fff;
  margin-bottom: 40px;
  font-size: 3rem;
}

#library {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  display: grid;
}

.book {
  aspect-ratio: 1 / 1.5;
  background-position: center;
  background-size: cover;
  border: 1px solid #aaa;
  flex-direction: column;
  justify-content: space-between;
  transition: all .4s;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 8px #1e1e1eb3;
}

.book:hover {
  transform: scale(1.1);
  box-shadow: 0 12px 12px #1e1e1e66;
}

.bookControls {
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  display: flex;
}

.checkbox {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 3px;
  align-self: flex-end;
  width: 30px;
  height: 30px;
}

.checkmark {
  opacity: 0;
  width: 100%;
  height: 100%;
}

.checkmark.read {
  opacity: 1;
}

.bookInfo {
  opacity: 0;
  padding: 10px;
  transition: all .4s;
  display: grid;
  transform: translateY(100%);
}

.book:hover .bookInfo {
  color: #fff;
  opacity: 1;
  backdrop-filter: blur(5px);
  background-color: #141414cc;
  transform: translateY(1px);
}

#addBtn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  transition: scale .3s;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

#addBtn:hover {
  scale: 1.2;
}

.removeBtn {
  opacity: 0;
  cursor: pointer;
  background-image: url("closeBtn.cae4def2.svg");
  width: 30px;
  height: 30px;
  transition: all .4s;
}

.book:hover .removeBtn {
  opacity: 1;
}

#newBooksWrapper {
  border: none;
  margin: auto;
}

::backdrop {
  background-color: #1e1e1ecc;
}

#newBooksWrapper form {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
}

#closeBtn {
  float: right;
  cursor: pointer;
  width: 30px;
  height: 30px;
  margin-left: 20px;
}

#elementContainer {
  gap: 10px;
  margin: 20px 0;
  display: grid;
}

.formElement {
  gap: 5px;
  display: grid;
}

.formElement.inline {
  display: block;
}

input {
  padding: 10px;
}

input[type="checkbox"] {
  margin-left: 0;
  margin-right: 5px;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #8ac11f;
  border: none;
  border-radius: 5px;
  align-self: self-end;
}

header {
  position: relative;
}

#sorting {
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
}
